//,file:size:(MB)
 function checkFiles(file,size) {
  var fileName,fileSize;
  size=size || 5;
  if (file == null || file.size == 0) {
    console.error("");
    return false;
  }
  fileSize=Math.ceil(file.size/1024/1024);
  fileName=file.name;
  if (fileSize > size) {
    console.error(""+size);
    return false;
  }
  //
  var sExp = fileName.substring(fileName.lastIndexOf('.'), fileName.length).toLowerCase(); //
  if (sExp != ".jpg" && sExp != ".gif" && sExp != ".png" && sExp != ".tif" && sExp != ".tiff") {
    console.error("");
    return false;
  }
  return true;
}

function trimStr(str){return str.replace(/(^\s*)|(\s*$)/g,"");}
export {checkFiles,trimStr}



